<template>

  <div class="modal-body">
    <a @click="$emit('close')"> <i class="ion ion-close-circled"></i></a>
    <h4 class="text-center">Add Family Member</h4>
    <br/>
    <member-form
      :firstName.sync="firstName"
      :lastName.sync="lastName"
      :dob.sync="dob"
      :accessCode.sync="accessCode"
      :errors="errors"
    >
      <hr>
      <div class="text-center">
        <button type="submit" class="btn btn-main" @click="createMember">Add Family Member</button>
      </div>
    </member-form>
  </div><!--/.modal-body-->
</template>

<script>
import MemberForm from '@/components/members/Form'
import {snakeToCamel} from '@/utils/case'

export default {
  components: {MemberForm},
  data () {
    return {
      dob: '',
      firstName: '',
      lastName: '',
      accessCode: '',
      errors: {}
    }
  },
  methods: {
    createMember () {
      const {
        dob,
        firstName,
        lastName,
        accessCode
      } = this
      this.$http.post('members.json', {
        member: {
          dob,
          facility_id: this.$store.state.familyFacility,
          first_name: firstName,
          last_name: lastName,
          access_code: accessCode
        }
      }).then(() => {
        this.$store.commit('setMembers', [])
        this.$emit('close')
      }, ({body}) => { this.errors = snakeToCamel(body) })
    }
  }
}
</script>



