<template>
  <div>
    <div class="login-form">
      <form>
        <vue-input type="email" v-model="email" label="Email Address" :className="error ? 'has-error' : ''"/>
        <vue-input type="password" v-model="password" label="Password" :className="error ? 'has-error' : ''" @enter="login"/>
        <span class="help-block" v-if="error"> {{ error }}</span>
        <a href="#" class="btn btn-main btn-block" @click.prevent="login">Log In</a>
      </form>
    </div>

    <div class="text-center">

      <p class="txt14">Don't have an account?
        <router-link :to="{ name: 'register'}">Register Here</router-link>
      </p>

      <p class="txt14"><a href="#" @click.prevent="$router.push({ name: 'reset-password' })">Forgot Password?</a></p>
    </div>
  </div>
</template>
<script>
import logo from '@/assets/logo.png'
import VueInput from '@/components/form/Input'

export default {
  components: {
    VueInput
  },
  data () {
    return {
      logo,
      email: '',
      password: '',
      error: ''
    }
  },
  methods: {
    login () {
      const {
        password,
        email,
        $store: { state: { familyFacility } }
      } = this
      this.$auth.login({
        rememberMe: false,
        body: {
          family_user: {
            email: email,
            password: password,
            facility_id: familyFacility
          }
        },
        success: ({body: { user }}) => {
          this.$store.commit('setMembers', user.members)
        },
        error: (resp) => {
          try {
            this.error = resp.body.error
          } catch (e) {
            this.error = 'Ooops!'
          }
        }
      })
    }
  }
}
</script>



