// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import VModal from 'vue-js-modal'

import 'bootstrap3/dist/css/bootstrap.css'
import 'ionicons/scss/ionicons.scss'
import '@/styles/main.scss'

import App from './App'
import router from './router'
import store from './store'
import VueInput from '@/components/form/Input'

import __token from '@websanova/vue-auth/src/lib/token'

Vue.use(VModal, { dynamic: true })
Vue.use(VueResource)
Vue.use(require('vue-moment'))
Vue.component('vue-input', VueInput)
Vue.http.options.root = 'http://synergy.live.menustreams-local.com:3000/api/family/v0'
Vue.router = router
Vue.use(require('@websanova/vue-auth'), {
  auth: require('@/auth/index.js'),
  http: require('@websanova/vue-auth/drivers/http/vue-resource.1.x.js'),
  router: require('@websanova/vue-auth/drivers/router/vue-router.2.x.js'),
  loginData: { url: 'users/sign_in.json' },
  registerData: { url: 'users.json' },
  logoutData: { url: 'users/sign_out.json', method: 'DELETE', redirect: '/login' },
  authRedirect: '/login',
  fetchData: {
    success ({ body }) {
      Vue.auth.user(body)
      store.commit('setCurrentMember', body.members[0])
    }
  },
  checkAuthenticated (cb) {
    if (this.watch.authenticated === null && __token.get.call(this)) {
      this.watch.authenticated = false

      if (this.options.fetchData.enabled) {
        this.options.fetchPerform.call(this, {
          success: cb,
          error: cb,
          enabled: true
        })
      } else {
        this.options.fetchProcess.call(this, {}, {})
        return cb.call(this)
      }
    } else {
      this.watch.loaded = true
      return cb.call(this)
    }
  }
})

Vue.http.interceptors.push((request, next) => {
  next((res) => {
    if (
      res.status === 401 &&
      ['UnauthorizedAccess', 'InvalidToken'].indexOf(res.data.code) > -1
    ) {
      Vue.auth.logout({
        redirect: { name: 'login' }
      })
    }
    // else if (res.status === 500) {
    //     Vue.router.push({name: 'error-500'});
    // }
  })
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



