import Vuex from 'vuex'
import Vue from 'vue'
import moment from 'moment'
import { snakeToCamel } from './utils/case'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    registration: {},
    background: null,
    logo: null,
    facility_id: 562,
    cname: null, // TODO: get from subdomain
    familyFacility: null,
    members: [],
    menus: [],
    mainFontColor: '#444',
    secondaryFontColor: '#ffd10a',
    currentDate: moment(),
    currentMember: {}
  },
  mutations: {
    setCurrentMember (state, member) {
      if (member) state.currentMember = snakeToCamel(member)
    },
    setCurrentDate (state, date) {
      state.currentDate = date
    },
    setMenus (state, menus) {
      state.menus = menus
    },
    setRegistration (state, o) {
      state.registration = o
    },
    setLogo (state, o) {
      state.logo = o.url
    },
    setFamilyFacility (state, o) {
      state.familyFacility = o
    },
    setBackground (state, o) {
      state.background = o.url
      document.documentElement.style.setProperty('--main-bg-image', `url(${state.background})`)
    },
    setMembers (state, members) {
      state.members = members
    },
    setMainFontColor (state, color) {
      if (color) {
        state.mainFontColor = color
        document.documentElement.style.setProperty('--main-font-color', state.mainFontColor)
      }
    },
    setSecondaryFontColor (state, color) {
      if (color) {
        state.mainFontColor = color
        document.documentElement.style.setProperty('--secondary-color', state.secondaryFontColor)
      }
    }
  },
  actions: {
    setCurrentMember ({commit, dispatch}, member) {
      commit('setCurrentMember', member)
      dispatch('getMenus', member.id)
    },
    setCurrentDate ({commit, dispatch, state}, date) {
      commit('setCurrentDate', moment(date))
      dispatch('getMenus', state.currentMember.id)
    },
    incrementCurrentDate ({commit, dispatch, state}) {
      commit('setCurrentDate', moment(state.currentDate).add(3, 'days'))
      dispatch('getMenus', state.currentMember.id)
    },
    decrementCurrentDate ({commit, dispatch, state}) {
      commit('setCurrentDate', moment(state.currentDate).subtract(3, 'days'))
      dispatch('getMenus', state.currentMember.id)
    },
    getMembers ({commit, state}) {
      if (!state.members.length) {
        Vue.http.get('members.json').then(
          ({body}) => {
            commit('setMembers', body)
            commit('setCurrentMember', body[0])
          })
      }
    },
    getMenus ({state, commit}, id) {
      Vue.http.get(`members/${id}/menus`, {
        params: {
          start_date: state.currentDate.clone().format('DD/MM/YYYY'),
          end_date: state.currentDate.clone().add(3, 'days').format('DD/MM/YYYY')
        }
      }).then(
        ({body}) => {
          commit('setMenus', body)
        }
      )
    }
  }
})



