<template>
  <div></div>
</template>
<script>
export default {
  created () {
    const { $route: { query }, $auth } = this
    this.$auth.token(null, `${query['token']};${query.email};${query.facility_id}`)
    this.$auth.watch.authenticated = true
    // this.$auth.fetch()
    if ($auth.ready()) {
      $auth.fetch({
        success () {
          this.$router.push({name: 'profile'})
        }
      })
    } else {
      console.log('asd')
    }
  }
}
</script>



