<template>
  <div class="container">
    <header>
      <div class="row">
        <div class="col-md-3 company-logo">
          <img :src="logo" alt="" title=""/>
        </div>
        <div class="col-md-9 header-right">
          <div class="row text-right padtb20">
            <div class="col-sm-6 col-md-8">
              <div class="form-group pad10">
                <datepicker
                  v-model="date"
                  :bootstrap-styling="true"
                  wrapperClass="date"
                  inputClass="input-sm"
                  placeholder="Search Date"
                >
                      <span slot="afterDateInput" class="input-group-addon">
                        <i class="ion ion-calendar"></i>
                      </span>
                </datepicker>
              </div>
            </div>
            <div class="col-sm-6 col-md-4">
              <div class="padtb20 navigation">
                <a href="#" title="Add Family Member" @click.prevent="openMemberModal"><i
                  class="ion ion-android-person-add"></i></a>
                <a href="#" title="Sign Out" @click.prevent="$auth.logout({ makeRequest: true })"><i
                  class="ion ion-ios-locked"></i></a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>

    <div class="row">
      <div class="col-xs-12 patient-name relative">
        <div class="padtb20">
          <h1>{{ fullName }} | <span>{{ currentMember.location }}</span> | <a href="#" @click="openMemberListModal"><i
            class="ion ion-android-people"></i></a></h1>
          <div class="day-nav">
            <a href="#" class="prev" @click="decrementCurrentDate"><i class="ion ion-arrow-left-c"></i> PAST 3 DAYS</a>
            <a href="#" class="next" @click="incrementCurrentDate">NEXT 3 DAYS <i class="ion ion-arrow-right-c"></i></a>
          </div>
        </div>
      </div>

      <menu-day v-for="(menu, i) in menus" :key="i" :menus="menu"/>

      <div class="col-xs-12">
        <div class="padtb20 mtb20 border-top">
          <p class="txt12"><strong>Disclaimer</strong><br/>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed bibendum finibus ante, a imperdiet massa
            pretium vestibulum. Aenean hendrerit blandit mi non viverra. Quisque ligula diam, convallis sit amet
            sagittis at, finibus non urna. Phasellus faucibus bibendum tellus eu rutrum. Nullam varius diam id ipsum
            consequat, eget hendrerit leo placerat. Integer dignissim nulla orci, vel fermentum libero viverra vitae. Ut
            volutpat fringilla ante laoreet feugiat. Curabitur id diam ac urna ultricies posuere vitae eu felis.
            Suspendisse commodo, lorem in egestas auctor, odio nibh lobortis sapien, eu sodales quam massa porta mi.</p>
        </div>
      </div>
    </div><!--/.row-->
  </div><!--/.container-->

</template>

<script>
import datepicker from 'vuejs-datepicker'
import {mapState, mapActions} from 'vuex'

import MemberModal from '@/components/modals/Member'
import MembersListModal from '@/components/modals/MembersList'
import logo from '@/assets/logo.png'
import MenuDay from '@/components/menus/Day'

export default {
  components: {
    MemberModal,
    MembersListModal,
    MenuDay,
    datepicker
  },
  data () {
    return {
      logo,
      date: ''
    }
  },
  methods: {
    openMemberModal () {
      this.$modal.show(MemberModal, {}, {height: 'auto'})
    },
    openMemberListModal () {
      this.$modal.show(MembersListModal, {}, {height: 'auto'})
    },
    ...mapActions(['setCurrentDate', 'incrementCurrentDate', 'decrementCurrentDate'])
  },
  watch: {
    date (val) {
      this.setCurrentDate(val)
    }
  },
  created () {
    this.$store.dispatch('getMenus', this.currentMember.id)
  },
  computed: {
    fullName () {
      const { fname, lname } = this.currentMember
      return `${fname} ${lname}`
    },
    ...mapState(['menus', 'currentMember'])}
}
</script>

<style>
  .vdp-datepicker {
    position: absolute;
  }
</style>



