<template>
  <div :class="'form-group ' + (hasError ? 'has-error': '') + ' ' + className">
    <label class="control-label" >{{ label }}</label>
    <br/>
    <input :type="type" class="form-control" @input="$emit('input', $event.target.value)" @keyup.enter="$emit('enter')">
    <span class="help-block" v-if="hasError" v-for="(err, idx) in error" :key="idx"> {{ err }}</span>
  </div>
</template>

<script>
export default {
  props: {
    label: '',
    type: {
      default: 'text'
    },
    error: '',
    className: ''
  },
  computed: {
    errors () {
      return [].concat(this.error)
    },
    hasError () {
      const { error } = this
      if (typeof error === 'string') {
        return error
      } else if (Array.isArray(error)) {
        return error.length
      }
    }
  }
}
</script>



