<template>
  <div :class="'col-sm-4 border-left border-right ' + meal.toLowerCase()">
    <div class="pad20">
      <h3 class="border-bottom mb20">{{ meal }}</h3>

      <!-- THE GUEST WILL NEVER BE ALL THREE OF THESE, ONLY 1. WE'VE JUST INCLUDED THEM ALL FOR SAMPLE UI -->
      <ul class="list-unstyled border-bottom" v-if="status">
        <li v-if="status === 'refused'"><p class="txt14"><img :src="refused" align="middle"/> REFUSED MEAL</p></li>
        <li v-if="status === 'not_present'"><p class="txt14"><img :src="iconNotPresent" align="middle"/> NOT PRESENT</p></li>
        <li v-if="status === 'out_of_building'"><p class="txt14"><img :src="iconOut" align="middle"/> OUT OF BUILDING</p></li>
      </ul>

      <ul v-else class="list-unstyled border-bottom">
        <li v-for="item in menuItems" :key="item.id">{{ item.name }}</li>
      </ul>
      <div class="dislikes mtb10" v-if="!status">
        <div class="no-gutter clearfix">
          <div class="col-xs-12 text-center border-right">
            <a @click="openFoodOptionsModal">
              <div class="pad10"><a href="#" >VIEW FULL MENU</a></div>
            </a>
          </div>

        </div>
      </div>

    </div><!--/.pad20-->
  </div>

  <!--<div class="col-sm-4 dinner">-->
    <!--<div class="pad20">-->
      <!--<h3 class="border-bottom mb20">Dinner</h3>-->

      <!--<ul class="list-unstyled border-bottom">-->
        <!--<li>Chive Whipped Potatoes</li>-->
        <!--<li>Stewed Tomatoes</li>-->
        <!--<li>Rye Bread</li>-->
        <!--<li>Raspberry Jelly Roll</li>-->
      <!--</ul>-->

      <!--<div class="dislikes mtb10">-->
        <!--<div class="no-gutter clearfix">-->
          <!--<div class="col-xs-12 text-center border-right">-->
            <!--<a href="#dislike-info" data-toggle="modal">-->
              <!--<div class="pad10"><a href="#food-options" data-toggle="modal">VIEW FULL MENU</a></div>-->
            <!--</a>-->
          <!--</div>-->

        <!--</div>-->
      <!--</div>-->

    <!--</div>&lt;!&ndash;/.pad20&ndash;&gt;-->
  <!--</div>&lt;!&ndash;/.dinner&ndash;&gt;-->
</template>

<script>
import refused from '@/assets/icon-refuse.png'
import iconNotPresent from '@/assets/icon-not-present.png'
import iconOut from '@/assets/icon-out.png'
import FoodOptionsModal from '@/components/modals/FoodOptions'

export default {
  components: { FoodOptionsModal },
  data () {
    return {
      refused,
      iconNotPresent,
      iconOut
    }
  },
  props: {
    meal: '',
    menuItems: {
      type: Array,
      default: () => []
    },
    status: '',
    date: ''
  },
  methods: {
    openFoodOptionsModal () {
      const {
        meal,
        date
      } = this
      this.$modal.show(FoodOptionsModal, {
        meal,
        date
      }, {height: 'auto'})
    }
  },
  computed: {
  }
}
</script>



