<template>
  <div class="modal-body">
    <a @click="$emit('close')"> <i class="ion ion-close-circled"></i></a>

    <h4 class="text-center">{{ meal }} OPTIONS</h4>
    <p class="txt14 text-center">({{date | moment('dddd, MMMM d, Y')}})</p>

    <div class="row padtb20">
      <div class="col-sm-6">
        <ul class="list-unstyled txt14">
          <li class="text-uppercase"><strong>Juice</strong></li>
          <li>125 mL Orange Juice</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Fruit</strong></li>
          <li>1 Each Banana</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Cereal</strong></li>
          <li>180 ML High Fibre Oatmeal</li>
          <li>180 mL Special K Cereal</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Breakfast Sides</strong></li>
          <li>30 mL Peanut Butter</li>
          <li>60 mL Cottage Cheese</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Bread</strong></li>
          <li>2 Slice Raisin Toast</li>
          <li>2 Slice Whole Wheat Toast</li>
          <li>&nbsp;</li>
        </ul>
      </div>
      <div class="col-sm-6">
        <ul class="list-unstyled txt14">
          <li class="text-uppercase"><strong>Condiment</strong></li>
          <li>1 Each Margarine</li>
          <li>1 Each Raspberry Jam</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Beverage</strong></li>
          <li>250 mL 2% Milk</li>
          <li>175 mL Decaf Coffee</li>
          <li>180 mL Hot Tea</li>
          <li>&nbsp;</li>

          <li class="text-uppercase"><strong>Extra Items</strong></li>
          <li>237 mL Boost 1. 5 Pluss Vanilla</li>
          <li>&nbsp;</li>
        </ul>
      </div>
    </div>
  </div><!--/.modal-body-->
</template>

<script>
export default {
  props: {
    meal: '',
    date: ''
  }
}
</script>



