module.exports = {
  tokens: ['x-user-token', 'x-user-email', 'x-user-facility-id'],

  request: function (req, token) {
    let headers = {}
    let tokens = token.split(';')

    const auth = this.options.deviseAuth || this.options.auth
    auth.tokens.forEach(function (tokenName, index) {
      if (tokens[index]) {
        headers[tokenName] = tokens[index]
      }
    })

    this.options.http._setHeaders.call(this, req, headers)
  },

  response: function (res) {
    let token = []
    let headers = this.options.http._getHeaders.call(this, res)

    if (headers['x-user-token']) {
      const auth = this.options.deviseAuth || this.options.auth
      auth.tokens.forEach(function (tokenName) {
        if (headers[tokenName]) {
          token.push(headers[tokenName])
        }
      })

      return token.join(';')
    }
  }
}



