import Vue from 'vue'
import Router from 'vue-router'
import Logo from '@/components/Logo'
import {
  Profile,
  Login,
  Register,
  Disclaimer,
  ResetPassword,
  Callback
} from '@/components/pages'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'profile',
      component: Profile,
      meta: { auth: true }
    },
    {
      path: '/login',
      name: 'login',
      components: { default: Login, logo: Logo }
    },
    {
      path: '/register',
      name: 'register',
      components: { default: Register, logo: Logo }
    },
    {
      path: '/disclaimer',
      name: 'disclaimer',
      components: { default: Disclaimer, logo: Logo }
    },
    {
      path: '/reset-password',
      name: 'reset-password',
      component: ResetPassword
    },
    {
      path: '/callback',
      name: 'callback',
      component: Callback,
      meta: { auth: false }
    }
  ]
})



