<template>
  <div class="modal-body">
    <a @click="$emit('close')"> <i class="ion ion-close-circled"></i></a>
    <h4 class="text-center">Choose a Family Member</h4>
    <br/>
    <div class="family-member relative clearfix border-bottom padtb20" v-for="member in members" :key="member.id">
      <h4>{{ `${member.fname} ${member.lname}` }} | <span>{{ member.location }}</span></h4>
      <a href="#" class="btn btn-secondary" @click="$store.dispatch('setCurrentMember', member); $emit('close')"> View Details</a>
    </div>
  </div><!--/.modal-body-->
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
    }
  },
  computed: {
    ...mapState(['members'])
  },
  mounted () {
    this.$store.dispatch('getMembers')
  }
}
</script>



