<template>
  <div>
    <div class="row">
      <div class="col-sm-6">
        <vue-input v-model="modelFirstName" label="First Name" :className="errors['firstName'] ? 'has-error' : ''" :error="errors['firstName']"/>
      </div>
      <div class="col-sm-6">
        <vue-input v-model="modelLastName" label="Last Name" :className="errors['lastName'] ? 'has-error' : ''" :error="errors['lastName']"/>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-3">
        <label>Date of Birth</label>
        <vue-select v-model="month" :options="months" ignoreFirst/>
      </div>
      <div class="col-sm-3">
        <label>&nbsp;</label>
        <vue-select v-model="day" :options="days" ignoreFirst/>
      </div>
      <div class="col-sm-3">
        <label>&nbsp;</label>
        <vue-select v-model="year" :options="years" ignoreFirst/>
      </div>
      <div class="col-sm-3">
        <vue-input v-model="modelAccessCode" label="Access Code" :className="errors['accessCode'] ? 'has-error' : ''" :error="errors['accessCode']"/>
      </div>
    </div>
    <slot/>
  </div>
</template>

<script>
import VueSelect from '@/components/Select'
import VueInput from '@/components/form/Input'
export default {
  components: { VueSelect, VueInput },
  data () {
    const year = 'Year'
    const month = 'Month'
    return {
      day: 'Day',
      month: month,
      year,
      years: [year].concat(this.calculateYearsBetween(new Date('01/01/1900'))),
      months: [month].concat(['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']),
      modelAccessCode: this.accessCode,
      modelFirstName: this.firstName,
      modelLastName: this.lastName
    }
  },
  computed: {
    days () {
      let days = new Date(Date.UTC(this.year, this.getMonthNumber(), 0)).getDate()
      if (isNaN(days)) days = 31
      return [this.day].concat(Array.from({length: days}, (_k, i) => i + 1))
    }
  },
  methods: {
    getMonthNumber () {
      return this.months.findIndex(e => e === this.month)
    },
    setDob () {
      this.$emit('update:dob', new Date(Date.UTC(this.year, this.getMonthNumber() - 1, this.day)))
    },
    calculateYearsBetween (startDate = new Date(), endDate = new Date()) {
      let startYear = startDate.getYear() + 1900

      let endYear = endDate.getYear() + 1900
      const output = []
      for (let year = startYear; year <= endYear; year++) output.push(year)
      return output
    }
  },
  watch: {
    day () { this.setDob() },
    month () { this.setDob() },
    year () { this.setDob() },
    modelAccessCode (val) { this.$emit('update:accessCode', val) },
    modelFirstName (val) { this.$emit('update:firstName', val) },
    modelLastName (val) { this.$emit('update:lastName', val) }
  },
  props: {
    firstName: '',
    lastName: '',
    accessCode: '',
    errors: {
      type: Object,
      default: () => { return {} }
    }
  }
}
</script>



