<template>
  <div>
    <div class="col-xs-12">
      <!--THURSDAY, MARCH 15, 2018-->
      <h2>{{ new Date(getFirstKey(menus)) | moment('dddd, MMMM d, Y') }}</h2>
    </div>
    <div class="col-xs-12 meal-times">
      <div class="bg-grey clearfix">
        <menu-card
          v-for="(menu, key, i) in menus[getFirstKey(menus)]"
          :key="i"
          :meal="key"
          :menuItems="menu.order_items"
          :status="menu.status"
          :date="new Date(getFirstKey(menus))"
        />
      </div>
    </div>
  </div>
</template>

<script>
import MenuCard from '@/components/menus/Card'
export default {
  components: { MenuCard },
  props: {
    menus: {}
  },
  methods: {
    getFirstKey (obj) {
      return Object.keys(obj)[0]
    }
  }
}
</script>



