<template>
  <select class="form-control" @change="$emit('input', $event.target.value)">
    <option
      v-for="(option, idx) in options"
      :key="idx"
      :hidden="ignoreFirst && (idx === 0)"
      :value="option"
      :selected="option == value"
    >{{ option }}</option>
  </select>
</template>

<script>
export default {
  props: {
    options: {
      type: Array
    },
    value: '',
    ignoreFirst: {
      type: Boolean,
      default: false
    }
  }
}
</script>



