<template>
  <div class="login-form">
    <form @submit.prevent="sendNewPassword" v-if="token">
      <vue-input type="password" v-model="password" label="Enter new password" :error="errors['password']"/>
      <span v-if="errors['reset_password_token']" class="help-block">Token {{ errors['reset_password_token'][0] }}</span>
    </form>
    <form @submit.prevent="send" v-else>
      <vue-input type="email" v-model="email" label="Enter your email"  :error="errors['email']"/>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      email: '',
      password: '',
      token: '',
      errors: {}
    }
  },
  methods: {
    send () {
      this.errors = {}
      this.$http.post('users/password', {
        family_user: {
          email: this.email,
          facility_id: this.$store.state.familyFacility
        }
      }).then(() => {}, ({body}) => { this.errors = body.errors })
    },
    sendNewPassword () {
      this.errors = {}
      this.$http.put('users/password', {
        family_user: {
          password: this.password,
          reset_password_token: this.token
        }
      }).then(
        ({ body }) => {
          this.$router.push({
            name: 'callback',
            query: body
          })
        },
        ({body}) => { this.errors = body.errors }
      )
    }
  },
  created () {
    this.token = this.$route.query.token
  }
}
</script>



