<template>
  <div v-if="$store.state.familyFacility && loading === true">
    <router-view name="logo"/>
    <router-view/>
    <modals-container/>
  </div>
  <div v-else>
    <div class="container" v-if="loading">
      No Active Portal Found
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      loading: false
    }
  },
  methods: {
    getFacility () {
      this.loading = false
      this.$http.get('facilities/settings', {
        params: {
          facility_id: this.$store.state.facility_id
        }
      }).then(({body}) => {
        this.$store.commit('setLogo', body.logo)
        this.$store.commit('setBackground', body.background_image)
        this.$store.commit('setFamilyFacility', body.id)
        this.$store.commit('setMainFontColor', body.main_font_color)
        this.$store.commit('setSecondaryFontColor', body.secondary_font_color)
      }).finally(() => { this.loading = true })
    }
  },
  created () {
    this.getFacility()
  }
}
</script>



