<template>
  <div class="register-form">
    <h4 class="text-center">YOUR INFORMATION</h4>
    <form>

      <div class="row">
        <div class="col-sm-6">
          <vue-input v-model="firstName" label="First Name" :error="errors['firstName']"/>
        </div>
        <div class="col-sm-6">
          <vue-input v-model="lastName" label="Last Name" :error="errors['lastName']"/>
        </div>
      </div>

      <div class="row">
        <div class="col-sm-6">
          <vue-input type="email" v-model="email" label="Email Address" :error="errors['email']"/>
        </div>
        <div class="col-sm-6">
          <vue-input v-model="phone" label="Phone Number" :error="errors['phone']"/>
        </div>
      </div>

      <div class="row">
        <div class="col-sm-6">
          <vue-input type="password" v-model="password" label="Password" :error="errors['password']"/>
        </div>
        <div class="col-sm-6">
          <vue-input type="password"
                     v-model="passwordConfirmation"
                     label="Confirm Password"
                     :error="errors['passwordConfirmation']"
          />
        </div>
      </div>
      <hr/>

      <h4 class="text-center">FAMILY MEMBER'S INFORMATION</h4>
        <template v-for="(member, idx) in members">
          <member-form
            :key="idx"
            :firstName.sync="member.firstName"
            :lastName.sync="member.lastName"
            :dob.sync="member.dob"
            :accessCode.sync="member.accessCode"
          />
          <hr :key="idx + 'h'"/>
        </template>
      <div class="text-center">
        <p class="mb40">
          <a href="#" @click.prevent="members.push({})">
            <i class="ion ion-android-person-add"></i> Add another family member
          </a>
        </p>
        <button class="btn btn-main" @click.prevent="checkAndGo">Register</button>
      </div>
    </form>
  </div><!--/.register-form-->

</template>

<script>
import MemberForm from '@/components/members/Form'
import VueInput from '@/components/form/Input'
import { snakeToCamel } from '@/utils/case'

export default {
  components: { MemberForm, VueInput },
  data () {
    return {
      errors: {},
      lastName: '',
      firstName: '',
      email: '',
      phone: '',
      password: '',
      passwordConfirmation: '',
      members: [{}]
    }
  },
  methods: {
    checkAndGo () {
    //  TODO: add validations
      const {
        lastName,
        firstName,
        email,
        password,
        passwordConfirmation,
        phone,
        members
      } = this
      this.$store.commit('setRegistration', {
        members
      })
      this.errors = {}
      this.$auth.register({
        rememberMe: false,
        body: {
          family_user: {
            phone,
            facility_id: this.$store.state.familyFacility,
            last_name: lastName,
            first_name: firstName,
            email,
            password,
            password_confirmation: passwordConfirmation,
            members_attributes: [] // members.map(e => camelToSnake(e))
          }
        },
        autoLogin: false,
        redirect: { name: 'disclaimer' },
        error: ({ body }) => {
          this.errors = snakeToCamel(body.errors)
        }
      })
    }
  }
}
</script>



